button{
  background-color: #008CBA;
  border: darkblue;
  color: white;
  padding: 10px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  font-family: 'Josefin Sans', sans-serif;
  font-family: 'Sriracha', cursive;
}
.button:hover {background-color: #008CBA}


input[type=url], select {
    width: 40%;
